.home-whatido {
    padding-top: 30px;
    padding-bottom: 30px;
    min-height: 20vh;
    background: url('https://raw.githubusercontent.com/yagoestevez/fcc-portfolio/master/src/Images/Stars.svg?sanitize=true') fixed ;
    background-color: #000000;

    .whatido-card {
        text-align: center;
        padding-top: 30px;
        padding-bottom: 30px;
        min-height: 60vh;
        border-top: 5px solid $color-estetic;
        border-bottom: 5px solid $color-estetic;

        &-icon {
            color: #ffffff;
            font-size: 120px;
            padding-bottom: 15px;

            @include _sm {
                font-size: 80px;
            }

            
        }

        &-subtitle {
            color: #ffffff;
            font-size: 50px;
            text-transform: uppercase;
            font-family:  $ff-monoton, cursive;

            @include _sm {
                font-size: 30px;
            }
        }

        &-text {
            color: #ffffff;
            font-size: 24px;
            font-family: $ff-audiowide, cursive;

            @include _sm {
                font-size: 16px;
            }
        }
    }
}