.header {
    position: relative;
    overflow: hidden;
    
    &-content {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        padding-top: 50px;

        max-width: 100%;
        height: 70vh;
    }

    &-text {
        max-width: 50%;
        text-align: center;

        @include _md {
            min-width: 100%;
        }
    }

    &-title {
        font-size: 40px;
        margin-bottom: 16px;
        text-transform: uppercase;
        font-weight: 700;
        font-family: $ff-2p, cursive;
        color: #ffffff;

        @include _md {
            font-size: 20px;
        } 
    }

    &-subtitle {
        font-family: $ff-2p, serif;
        color: #ffffff;
        font-size: 30px;
        
        @include _md {
            font-size: 25px;
        } 

        @include _sm{
            font-size: 16px;
        };

    }
    
    @import './navbar.scss';

    
}



.navbar-wrapper {
    position: fixed;
    height: 100vh;
    width: 40vw;
    background-color: #000000;
    z-index: 4;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 20px;
    right: -50vw;
    transition:  0.4s;

    @include _sm {
        width: 60vw;
        right: -80vw;
    }


    @include _md{
        padding: 20px 15px;
    }
    
    &.active {
        right: 0;
        
        @include _sm {
            right: 0;
        }
    }
}

.navbar-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-top: 50px;
}
    
.navbar-list-item {
    color: $color-grey;
    font-size: 25px;
    position: relative;
    font-family: $ff-2p, sans-serif;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-right: 10px;
    
    @include _md {
        font-size: 16px;
    }

    
}

.navbar-link {
    color: $color-grey;
}
                        
.navbar-link:hover {
    color: $color-estetic;
    text-decoration: none;
}

[data-navbar-toggler] {
    // d:none
    display: inline-block;
    // ml:a
    margin-left: auto;

    @include _lg {
        display: inline-block;
    }

    
}

