.objects {
    overflow: hidden;
}

@-moz-keyframes rocket-movement { 100% {-moz-transform: translate(600px, -50px);} }
@-webkit-keyframes rocket-movement {100% {-webkit-transform: translate(600px, -100px); } }
@keyframes rocket-movement { 100% {transform: translate(100px, -100px);} }
@-moz-keyframes spin-earth { 100% { -moz-transform: rotate(-360deg); transition: transform 20s;  } }
@-webkit-keyframes spin-earth { 100% { -webkit-transform: rotate(-360deg); transition: transform 20s;  } }
@keyframes spin-earth{ 100% { -webkit-transform: rotate(-360deg); transform:rotate(-360deg); transition: transform 20s; } }

@-moz-keyframes move-astronaut {
    100% { -moz-transform: translate(-160px, -160px);}
}
@-webkit-keyframes move-astronaut {
    100% { -webkit-transform: translate(-160px, -160px);}
}
@keyframes move-astronaut{
    100% { -webkit-transform: translate(-160px, -160px); transform:translate(-160px, -160px); }
}
@-moz-keyframes rotate-astronaut {
    100% { -moz-transform: rotate(-720deg);}
}
@-webkit-keyframes rotate-astronaut {
    100% { -webkit-transform: rotate(-720deg);}
}
@keyframes rotate-astronaut{
    100% { -webkit-transform: rotate(-720deg); transform:rotate(-720deg); }
}

@-webkit-keyframes spin-right{
    100% {
      -webkit-transform: rotate(360deg);
      -moz-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
      -o-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }

  @keyframes orbit {
    to {
      transform: rotate(45deg);
    }
  }

.objects img{
    z-index: 0;
    pointer-events: none;
}

.object_rocket{
    z-index: 0;
    position: absolute;
    transform: translateX(-50px);
    left: 20%;
    top: 190vh;
    animation: rocket-movement 10s linear infinite both;
    width: 120px;

    @include _600 {
        width: 50px;
    }

    @include _xs {
        top: 280vh;
    }
}

.object_earth{
    position: absolute;
    top: 130vh;
    left: 2%;
    z-index: 0;
    animation: spin-earth 30s infinite linear both;
    width: 100px;

    @include _600 {
        width: 70px;
    }

    @include _xs {
        top: 180vh; 
    }
}

.object_moon{
    position: absolute;
    top: 125vh;
    left: 10%;
    width: 90px;
    -webkit-animation: spin-right 15s linear infinite;
  animation: orbit 15s linear infinite;
    
  @include _600 {
    width: 60px;
 } 

    @include _xs {
        top: 175vh;
    }
}



.object_astronaut{
    animation: rotate-astronaut 50s infinite linear both alternate;
    width: 200px;

    @include _600 {
        width: 150px;
    }
}

.box_astronaut{
    z-index: 0;
    position: absolute;
    top: 170vh;
    right: 20%;
    will-change: transform;
    animation: move-astronaut 50s infinite linear both alternate;

    @include _xs {
        top: 240vh;   
    }
}