::-webkit-scrollbar {
    width: 10px;
  }
  ::-webkit-scrollbar-track {
    background: #000000; 
  }
  ::-webkit-scrollbar-thumb {
    background: $color-estetic; 
  }
  

body {
    font-size: 14px;
    overflow-y: hidden;
    background-color: #000000;
}

h1,
h2,
h3,
h4 {
    margin: 0;
    line-height: 1;
}

p {
    margin: 0;
}

ul,
ol,
li {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

*:focus {
    outline: none;
    border: none;
}

a {
    color: $color-grey;
    text-decoration: none;
}

a:hover {
    // td:none
    color: $color-grey;
    text-decoration: none;
}
