.home-works {
    text-align: center;
    background-color: #000000;
    min-height: 60vh;
    padding-top: 70px;
    padding-bottom: 70px;
    position: relative;

    &-title {
        color: #ffffff;
        font-size: 60px;
        font-family: $ff-bungee, cursive;
        @include _xs {
            font-size: 40px;
        }
    }

    &-subtitle {
        color: #ffffff;
        font-size: 50px;
        font-family: $ff-monofett, cursive;

        @include _sm {
            font-size: 25px;
        }

        
    }

    @import "WorksCube.scss";

    .works-cube-text {
        padding-top: 20px;
        font-weight: 200;
        font-family: $ff-geo, serif;
        color: #ffffff52;
        width: 100%;
        height: 100%;
        display: flex;
        text-align: center;
        align-items: center;
        justify-content: center;
        font-size: 20px;
        margin-bottom: 20px;

        @include _sm {
            font-size: 16px;
        }

        
    }

    &-card-col {
        width: 100%;
        margin-bottom: 30px;
        margin-top: 30px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        position: relative;
        transition: 2.5s;

        &:hover {
            transition: 2s;
            .home-works-card-body {
                margin-top: 0%;
                background-color: rgba(0, 0, 0, 0.8);

                animation: border 2s;
                animation-fill-mode: forwards;
                
                @keyframes border {
                    25% {
                        border-bottom: 5px solid $color-estetic;
                    }
                    50% {
                        border-bottom: 5px solid $color-estetic;
                        border-right: 5px solid $color-estetic;
                    }
                    75% {
                        border-bottom: 5px solid $color-estetic;
                        border-right: 5px solid $color-estetic;
                        border-top: 5px solid $color-estetic;
                    }
                    100% {
                        border-bottom: 5px solid $color-estetic;
                        border-right: 5px solid $color-estetic;
                        border-top: 5px solid $color-estetic;
                        border-left: 5px solid $color-estetic;
                    }
                }
            }

            .home-works-card-img {
                border-top: 2px solid transparent;
            }

            .works-link-1, .works-link-2 {
                opacity: 1;
            }
        }
        
        .works-link-1 {
            position: absolute;
            bottom: 35%;
            opacity: 0;
            transition: 2s;
            z-index: 0;

            @include _sm {
                bottom: 38%;
            }
            @include _xs {
                bottom: 30%;
            }
        }
        .works-link-2 {
            position: absolute;
            bottom: 15%;
            opacity: 0; 
            transition: 2s;
            z-index: 0;

            @include _sm {
                bottom: 20%;
            }
            @include _xs {
                bottom: 10%;
            }
        }
    }

    &-card {
        background-color: #000000;
        position: relative;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        transition: 1s;
        margin-bottom: 80%;
        

        &-img {
            border-top: 2px solid $color-estetic;
            border-radius: 10px;
            transition: 1s;
            img {
                width: 100%;
                height: 170px;
                border-radius: 10px;
            }

        }

        &-body {
            position: absolute;
            margin-top: 70%;
            padding-top: 10%;
            padding-left: 15px;
            padding-right: 15px;
            text-align: left;
            border-bottom: 2px solid $color-estetic;
            border-left: 5px solid transparent;
            border-right: 5px solid transparent;
            border-top: 5px solid transparent;
            border-radius: 10px;
            transition: 0.5s;
            color: #ffffff;
            font-family: $ff-2p, serif;
            height: 100%;
            background-color: #000000;
            z-index: 1;
            width: 100%;

            @include _sm {
                margin-top: 60%;
            }

            @include _xs {
                margin-top: 85%;
            }
        }

        &-title {
            font-size: 20px;
        }

        &-subtitle {
            font-size: 10px;
            transition: 0.5s;

            span {
                color: $color-estetic;
                font-size: 8px;
                margin-top: 10px;
            }
        }
    }
}
