.navbar {
    background-color: #000000;

    .logo-text {
        display: flex;
        font-family: $ff-marker, serif;
        align-items: center;
        justify-content: center;

        p {
            a {
                font-size: 14px;
                color: $color-grey;

                &:hover {
                    text-decoration: none;
                }
            }
        }
    }

    .container {
        display: flex;
        flex-wrap: wrap;
    }
}
.logo {
    font-family: $ff-marker, cursive;
    font-size: 40px;
    color: $color-estetic;
    text-decoration: none;
}
