.btn, 
a.btn,
input.btn,
button.btn {
    border: unset;
    line-height: 1;

    display: inline-flex;
    justify-content: center;
    align-items: center;

    padding: unset;
    transition: all 0.3s;
    font-size: 16px;

    &:active {
        transform: translateY(2px);
    }

    &-toggler {
        // p10
        opacity: 1 !important;
        z-index: 3;
        position: absolute;
        left: -60%;
        text-decoration: none;
        padding-top: 10px;
        outline: none;
        border: none;


        &:focus {
            outline: none;
            border: none;
            box-shadow: none;
        }

        &.active {
            left: 10vw;
        }
        

        .hamburger-inner,
        .hamburger-inner::after,
        .hamburger-inner::before {
            background-color: $color-estetic !important;

            
        }
    }

    &:hover, &:focus {
        outline: none;
        border: none;
    }

    &-linker {
        width: 180px;
        height: 50px;
        border-radius: 30px;
        background-color: $color-estetic;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #ffffff;

        a {
            color: #ffffff;
            font-size: 10px;
            text-transform: uppercase;
            text-decoration: none;
            font-family: $ff-2p, serif;

            &:hover {
                color: $color-grey;
                text-decoration: none;
                outline: none;
            }
        }
    }
}
    