.tetris-box {
    position: absolute;
    top: 25%;
    left: 0%;
    margin: auto;
    text-align: center;
    display: flex;
    flex-direction: column;

    @include _md {
        top: 40%;
    }

    &-line {
        display: flex;
    }

    .elem {
      width: 60px;
      height: 60px;
      display: inline-block;
      margin: 5px;
      font-size: 40px;

      @include _md {
          font-size: 30px;
          width: 50px;
          height: 50px;
      }

      @include _xs {
          font-size: 20px;
          width: 25px;
          height: 25px;
      }
    }
    @keyframes elem-animation {
        25% {
            transform: scale(1.1);
            background-color: $color-estetic;
            color: #ffffff;
        }
        50% {
            background-color: #000000;
            color: rgba(255, 255, 255, 0.089)
        }
        100% {
            transform: scale(1);
        }
    }
    
    .elem-1 {
        color: #000000;
        background-color: #000000;
        display: flex;
        justify-content: center;
        align-items: center;
        animation: elem-animation 1s linear infinite;
    }
    
    .elem-2 {
        color: #000000;
        background-color: #000000;
        display: flex;
        justify-content: center;
        align-items: center;
        animation: elem-animation 1s linear infinite;
        animation-delay: 0.25s;
    }
    
    .elem-3 {
        color: #000000;
        background-color: #000000;
        display: flex;
        justify-content: center;
        align-items: center;
        animation: elem-animation 1s linear infinite;
        animation-delay: 0.5s;
    }
    
}
  