.about-me {
    overflow-x: hidden;
    @import './AboutMeFlyingObjects.scss';

    text-align: center;
    padding-top: 75px;
    padding-bottom: 75px;
    // background: url('../../../media/photos/about-me_bg.jpg') no-repeat center center / cover fixed;
    background: url("https://raw.githubusercontent.com/yagoestevez/fcc-portfolio/master/src/Images/Stars.svg?sanitize=true") fixed;
    background-color: #000000;
    
    @keyframes text-movement1 {
        0% {
            transform: rotate(2deg);
        }

        50% {
            transform: rotate(-2deg);
        }

        100% {
            transform: rotate(2deg);
        }
    }
    @keyframes text-movement2 {
        0% {
            transform: rotate(-2deg);
        }

        50% {
            transform: rotate(2deg);
        }

        100% {
            transform: rotate(-2deg);
        }
    }

    &-title {
        font-family: $ff-2p, cursive;
        color: #ffffff;
        font-size: 60px;
        z-index: 4;

        animation: text-movement1 10s linear infinite;

        @include _sm {
            font-size: 40px;
        }
    }
    
    &-subtitle {
        font-size: 24px;
        color: #ffffff;
        font-family: $ff-2p, cursive;

        animation: text-movement2 10s linear infinite;

        @include _sm {
            font-size: 16px;
        }
    }

    @keyframes card-movement { 
        0% {
            transform: rotate(-3deg);
        }

        50% {
            transform: rotate(3deg);
        }

        100% {
            transform: rotate(-3deg);
        }
    }

    
    &-card {
        position: relative;
        animation: card-movement 30s linear infinite;z

        .row {
            align-items: center;
        }
        
        box-shadow:0 1px 4px rgba(0, 0, 0, 0.3), 0 0 20px rgba(0, 0, 0, 0.1) inset;
            &:before, &:after{
                content:"";
                position:absolute;
                bottom:0;top:0px;left:0px;right:0px;
                z-index:1;
               -webkit-box-shadow:0 0 30px 2px $color-estetic;
                -moz-box-shadow:0 0 30px 2px $color-estetic;
                box-shadow: 0 0 30px 2px $color-estetic;
                border-radius: 20px;
              }


        &-background {
            border: 10px solid rgba(128, 128, 128, 0.548);
            background-color: rgba(247, 0, 255, 0.363);
            padding: 30px;
            border-radius: 20px;
        }

        &-information {
            width: 100%;
            text-align: left;
            min-height: 50vh;

            @include _sm {
                min-height: 40vh;
            }
        }

        &-list {
            padding: 10px;
            &-item {
                margin-bottom: 20px;
                font-family: $ff-2p, serif;
                color: #ffffff;
                font-size: 20px;

                @include _sm {
                    font-size: 12px;
                    margin-bottom: 5px;
                }
            }
        }

        &-photo {
            display: flex;
            justify-content: center;
            align-items: center;height: 100%;
            

            @include _600 {
                height: 150px;
            }
        }
    }


    &-form {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        &-input {
            margin-bottom: 10px;
            background-color: rgba(0, 0, 0, 0);
            padding: 20px;
            outline: none;
            box-shadow: none;
            border: 5px solid rgba(255, 255, 255, 0.548);
            color: #ffffff;
            border-radius: 50%;

            &::placeholder {
                color: rgba(255, 255, 255, 0.404);
                font-size: 24px;
                font-family: $ff-geo, serif;
            }


            @include _sm {
                margin-bottom: 5px;
            }
        }

        &-submit {
            background-color: $color-estetic;
            color: #ffffff;
            border: 3px solid rgba(255, 255, 255, 0.548);
            padding: 20px 50px;
            border-radius: 50%;

            a {
                font-family: $ff-2p, serif;
                color: #ffffff;

            }
        }
    }

}
