.home-header {
  height: 100vh;
  background: url('../../../media/photos/home-header-bg.jpg') center center / cover fixed;
  background-color: rgba(0, 0, 0, 0.8);
  position: relative;
  overflow: hidden;


  @import "./HomeHeaderTetris.scss";
  @import "./HomeScrollMouse.scss";
  @import "./HomeScrollArrow.scss";
  .scroll-thing {
	  visibility: hidden;
    opacity: 0;
    &.active {
		opacity: 1;
		visibility: visible;
		transition: 2s;
    }
  }
}


