  .pink-cube {
    --main-color: #d400ff;
    --stroke-color: #525151;
    --light-color: #520063;
    --dark-color: #e359ff;
  }

  .compliance-svg {
    position: relative;
  }

  .cube-text {
    color: transparent;
    position: absolute;
    text-transform: uppercase;
    font-size: 24px;
    font-family: $ff-audiowide, cursive;
    animation: 2s cube-text alternate infinite;
    border-top: 1px solid transparent;
    border-bottom: 1px solid transparent;

    @include _sm {
      font-size: 20px;
    }

    @include _xs {
      font-size: 16px;
    }

    @keyframes cube-text {
      to {
        color: #ffffff;
        border-top: 1px solid rgba(225, 2, 255, 0.2);
        border-bottom: 1px solid rgba(225, 2, 255, 0.2);
      }
    }
  }

  .cube-up {
    right: 55%;
    top: 5%;
  }

  .cube-left {
    bottom: 0%;
    left: 5%;
  }

  .cube-right-1 {
    right: 0%;
    top: 45%;
  }

  .cube-right-2 {
    right: 0;
    top: 60%;
  }

  .stroke {
    fill: none;
    stroke: var(--stroke-color);
    stroke-miterlimit: 10;
    stroke-width: 0.6;
  }
  .face-up-down {
    fill: var(--main-color);
    opacity: 0.9;
  }
  .mup {
    animation: 2s moveUp alternate infinite;
  }
  .mleft {
    animation: 2s moveLeft alternate infinite;
    color: #ffffff;
  }
  .mright {
    animation: 2s moveRight alternate infinite;
  }
  
  @keyframes moveUp {
    to {
      transform: translateY(-35px);
    }
  }
  @keyframes moveRight {
    to {
      transform: translateX(50px);
    }
  }
  @keyframes moveLeft {
    to {
      transform: translateX(-50px);
    }
  }